.cv-card {
    transition: .3s, border .3s, border-radius .3s, box-shadow .3s;
    margin-top: 0;
    margin-bottom: 50px;
    padding: 65px 40px 60px;
    border-radius: 20px;
    box-sizing: border-box;
    box-shadow: 0px 6px 15px 0px #4a63e733;
    background-color: #ffffff;
    height: auto;
}

.cv-prof--im span {
    background-color: #555555;
    color: #fffffc;
    /* border: 1px solid #0bceaf; */
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 1px;
    font-weight: bold;
    padding: 10px 30px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 0px;
}

.cv-prof--img {
    height: 220px;
    width: 220px;
    border: 15px solid #55555524;
    border-radius: 50%;
}

.cv-prof--img img {
    height: 190px;
    width: 190px;
    border: 15px solid #5555554a;
}

.cv-prof--name,
.cv-prof--designation {
    margin: 8px 0;
}

.cv-cont-item--icon,
.cv-social--item {
    border: none;
}

.cv-dev {
    height: 300px;
    width: 300px;
}

.cv-skills--list {
    word-break: break-all;
    margin-bottom: 10px;
    margin-top: 10px;
}

.cv-skills--list ul {
    padding: 0;
    justify-content: space-between;
    flex-wrap: wrap;
}

li.cv-skill--item {
    margin: 0px 5px;
    border-radius: 4px;
    margin-bottom: 10px;
    padding: 4px 0px;
    display: inline-block;
}

.cv-skill--item span {
    border: 1px solid #0bceaf;
    padding: 4px 10px;
    border-radius: 4px;
}

.cv-description {
    margin: 0;
    text-align: justify;
    font-size: 15px;
}

.cv-skill--list ul {
    margin: 5px 30px 5px 0;
    padding-left: 20px;
}

.cv-skill--name {
    margin-bottom: 2px;
    font-size: 17px;
    font-weight: bold;
    justify-content: space-between;
}

.cv-skill--progress i {
    font-size: 12px;
    margin-top: -3px;
    margin-left: -2px;
    color: #0bceaf;
}

.cv-skill--progress {
    display: flex;
    height: 5px;
    background-color: #989696;
    border-radius: 4px;
}

.cv-skill--progress-bar {
    background-color: #0bceaf;
    border-radius: 4px;
}

.cv-whatido--grid,
.cv-edu--grid,
.cv-awards--grid {
    border: solid 1px #efefef;
    border-radius: 10px;
    padding: 30px 20px 22px;
}

li.cv-skill--progress-item {
    min-height: 55px;
}

.cv-card--awards-certificates .cv-card--body {
    padding: 0 20px;
    justify-content: space-evenly;
}

.cv-card--awards-certificates:first-child {
    justify-content: space-between;
}

.cv-card--awards-certificates .cv-card--body:nth-child(2) {
    flex: 1.5;
}

.cv-card--exprience .cv-proj--item{
    border-radius: 10px;
    margin-left: 15px;
    margin-top: 15px;
    border: solid 1px #efefef;
    padding: 15px;
}

.cv-proj--menu-item {
    flex: 1;
}

.cv-proj--menu-desc {
    flex: 4 1;
    text-align: justify;
    font-size: 15px;
    padding-right: 10px;
}

.cv-proj--menu-row{
    padding: 5px 0;
}

.cv-whatido--grid p {
    text-align: justify;
}